.submenu-panel {
	flex: 1;
	display: flex;
	flex-direction: column;
	width: var(--sidebar-width);
	height: 100%;
	overflow: hidden;
	--title-border-bottom: #aaa;
	user-select: none;
}

.bp3-dark .submenu-panel {
	--title-border-bottom: #777;
}

.submenu-panel-title {
	flex: 0;
	display: flex;
	align-items: center;
	font-size: 20px;
	font-weight: 400;	
	margin-top: 9px;
	border-bottom: 1px solid var(--title-border-bottom);
}

.submenu-panel-title :first-child {
	flex: 0;
}

.submenu-panel-title :not(:first-child) {
	padding: 14px 8px;
	flex: 1;
}

.submenu-panel-content {
	flex: 1;
	padding: 8px;
	height: 100%;
	overflow-y: auto;
}