 
	:root {
		--margin-left-page: 0.6cm;
		--margin-between-fields: 4px;
		--sans-font: 'Inter', 'Arial';
		--default-font-size: 11pt;
		--body-text-font-size: 16px;
		--margin-left-fieldset: 4px;
		--offset-flex-basis: 0;
		--largura-cabecalho: 100%;
		--body-text-max-width: 600px;
	}

	body {
		margin: 0;
		margin-left: var(--margin-left-page);
		padding: 0;
	}

  .autorizo {
  	position: absolute;
  	right: 0;
  	top: 10cm;
  }

  .autorizo > fieldset {
  	width: 2cm;
  	height: 2cm;
  }
	
	.cabecalho {
		font-family: var(--sans-font);
		font-size: var(--default-font-size);
		display: flex;
		flex-direction: column;
	}
	
	.posto-e-tipo {
		width: 100%;
		padding-right: 1cm;
		align-self: center;
		margin-left: 16px;
		margin-bottom: 1cm;
	}
	
	h2 {
		font-weight: 400;
		font-size: 20px;
		margin-top: 16px;
    margin-bottom: -4px;
	}
	
	h2 > b {
		font-weight: 700;
	}
	
	h3 {
		font-weight: 600;
		font-size: 16px;
		margin-bottom: -0.5cm;
	}

	.campos-expediente {
		width: var(--largura-cabecalho);
	}

	.campos-expediente legend {
		margin-left: -2px;
		padding-left: 2px;
		padding-right: 2px;
	}

	.flex-container {
		display: flex;
		flex-direction: row; 
		margin-bottom: var(--margin-between-fields);
  }

	fieldset {
		display: inline-block;
		border: 1px solid black;
		border-radius: 4px;
		width: 100%;
		box-sizing: content-box;
		min-height: 34.2px;
		padding-top: 2px;
		padding-bottom: 6px;
	}
	
	fieldset ~ fieldset {
		margin-left: var(--margin-left-fieldset);
	}
	
	legend {
		font-size: calc(var(--default-font-size) - 3pt);
	}

	.c1-6 {
		flex-basis: calc(16.66% - var(--offset-flex-basis));
	}

	.c1-4 {
		flex-basis: calc(25% - var(--offset-flex-basis));
	}

	.c1-3 {
		flex-basis: calc(33.33% - var(--offset-flex-basis));
	}
	
	.c1-2 {
		flex-basis: calc(50% - var(--offset-flex-basis));
	}

	.c2-3 {
		flex-basis: calc(66.67% - var(--offset-flex-basis));
	}

	.c1-1 {
		flex-basis: 100%;
	}	

  .corpo-texto {
  	font-family: 'Source Serif Pro', 'Georgia', 'Times New Roman';
  	font-size: var(--body-text-font-size);
  	line-height: 140%; 
  	margin-top: 0.5cm;
  	margin-right: 0.5cm;
  	padding-right: 0.5cm;
  	max-width: var(--body-text-max-width);
  }
	
	p.rtm,
	p.destinatarios {
		font-family: var(--sans-font);
		font-size: var(--default-font-size);
		line-height: 115%;
	}

  p.resumo {
  	font-family: var(--sans-font);
  	font-size: calc(var(--default-font-size) + 1pt);
  	margin-top: 28pt;
  	position: relative;
  }

  p.resumo:not(:empty):before {
  	content: "Resumo";
    position: absolute; 	
    font-size: calc(var(--default-font-size) - 1pt);
    top: -12pt;
    left: 0;
  }

  span.proposito:not(:empty) {
  	font-family: var(--sans-font);
  	font-weight: 600;
  }

  span.proposito:not(:empty):after {
  	content: '. ';
  }

  p.redator {
  	font-size: calc( var(--body-text-font-size) - 1.5pt );
  	font-weight: 500;
  }
	
	p.autor {
		font-family: var(--sans-font);
		font-size: calc(var(--default-font-size) - 1pt);
	}

	p.autor.floating {
		position: absolute;
		width: 2.0cm;
		bottom: 5cm;
		right: 0.5cm;
	}

  div.footer {
  	position: fixed;
  	height: 24px;
  	bottom: 14px;
  	left: var(--margin-left-page);
  	right: 0;
  	font-family: var(--sans-font);
  	background-color: white; 
  }	

  div.footer fieldset {
  	height: 20px;
  	background-color: white;
  }

  td.content-space-below {
  	height: 40px;
  }

  .tci {
  	margin-top: 40px;
  	font-family: var(--sans-font);
  	font-size: calc( var(--default-font-size) - 1pt );
/*
  	border-top: 1.45px dotted black;
*/  	
  	background-color: white;

  }

  .tci td {
  	font-size: calc( var(--default-font-size) - 1pt );
  	height: 0.75cm;
  	vertical-align: bottom;
  }

  .tci h3 {
  	text-align: center;
  	font-weight: 700;
  }

  .tci h3 .light {
  	font-weight: 300;
  }

  td.border-bottom {
  	border-bottom: 1px dashed black;
  	width: 95%;
  }

  div.campolivre {
  	margin-top: 24px;
  	border-top: 1px dashed black;
  	font-size: 7pt;
  	text-align: center;
  }

  div.spacer {
  	width: 20px;
  }

  fieldset.bare legend {
  	font-size: 9pt;
  }

  @media print {
    .tci {
    	page-break-inside: avoid;
    	border-top: 0;
    	margin-top: 0;
    }
  }
