:root {
	--animation-duration: .3s;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  transform: translateX(-100%);
  z-index: 0;
}
	
.slider.right	{
	transform: translateX(100%);
}

.slide-in {
  -webkit-animation: slide-in .3s forwards;
          animation: slide-in .3s forwards;
  -webkit-animation: slide-in var(--animation-duration) forwards;
          animation: slide-in var(--animation-duration) forwards;
}
	
.slide-in.right {
	-webkit-animation: slide-in-right .3s forwards;
	        animation: slide-in-right .3s forwards;
	-webkit-animation: slide-in-right var(--animation-duration) forwards;
	        animation: slide-in-right var(--animation-duration) forwards;
}

.slide-out {
  -webkit-animation: slide-out .3s forwards;
          animation: slide-out .3s forwards;
  -webkit-animation: slide-out var(--animation-duration) forwards;
          animation: slide-out var(--animation-duration) forwards;
}
	
.slide-out.right {
	-webkit-animation: slide-out-right .3s forwards;
	        animation: slide-out-right .3s forwards;
	-webkit-animation: slide-out-right var(--animation-duration) forwards;
	        animation: slide-out-right var(--animation-duration) forwards;
}	
    
@-webkit-keyframes slide-in {
	0%   { transform: translateX(-100%); opacity: 0; }
  100% { transform: translateX(0%); opacity: 1; }
}	
    
@keyframes slide-in {
	0%   { transform: translateX(-100%); opacity: 0; }
  100% { transform: translateX(0%); opacity: 1; }
}

@-webkit-keyframes slide-in-right {
	0%   { transform: translateX(100%); opacity: 0;  }
	100% { transform: translateX(0%); opacity: 1; }
}

@keyframes slide-in-right {
	0%   { transform: translateX(100%); opacity: 0;  }
	100% { transform: translateX(0%); opacity: 1; }
}
	

@-webkit-keyframes slide-out {
  0%   { transform: translateX(0%); opacity: 1; }
	80%  { opacity: 0; }
  99%  { transform: translateX(-100%); }
  100% { transform: scaleY(0); }
}
	

@keyframes slide-out {
  0%   { transform: translateX(0%); opacity: 1; }
	80%  { opacity: 0; }
  99%  { transform: translateX(-100%); }
  100% { transform: scaleY(0); }
}
	
@-webkit-keyframes slide-out-right {
	0%   { transform: translateX(0%); opacity: 1; }
	80%  { opacity: 0; }
	99%  { transform: translateX(100%); opacity: 0;  }
	100% { transform: scaleY(0); }
}
	
@keyframes slide-out-right {
	0%   { transform: translateX(0%); opacity: 1; }
	80%  { opacity: 0; }
	99%  { transform: translateX(100%); opacity: 0;  }
	100% { transform: scaleY(0); }
}	

