	.c1-3 {
		flex-basis: calc(33.33% - 0);
		flex-basis: calc(33.33% - var(--offset-flex-basis));
	}
	
	.c1-2 {
		flex-basis: calc(50% - 0);
		flex-basis: calc(50% - var(--offset-flex-basis));
	}

	.c2-3 {
		flex-basis: calc(66.67% - 0);
		flex-basis: calc(66.67% - var(--offset-flex-basis));
	}

	.c1-1 {
		flex-basis: 100%;
	}	

  .corpo-texto {
  	font-family: 'Source Serif Pro', 'Georgia', 'Times New Roman';
  	font-size: 16px;
  	font-size: var(--body-text-font-size);
  	line-height: 140%; 
  	margin-top: 0.5cm;
  	margin-right: 0.5cm;
  	padding-right: 0.5cm;
  	max-width: 600px;
  	max-width: var(--body-text-max-width);
  }
	
	p.rtm,
	p.destinatarios {
		font-family: 'Inter', 'Arial';
		font-family: var(--sans-font);
		font-size: 11pt;
		font-size: var(--default-font-size);
		line-height: 115%;
	}

  p.resumo {
  	font-family: 'Inter', 'Arial';
  	font-family: var(--sans-font);
  	font-size: calc(11pt + 1pt);
  	font-size: calc(var(--default-font-size) + 1pt);
  	margin-top: 28pt;
  	position: relative;
  }

  p.resumo:not(:empty):before {
  	content: "Resumo";
    position: absolute; 	
    font-size: calc(11pt - 1pt); 	
    font-size: calc(var(--default-font-size) - 1pt);
    top: -12pt;
    left: 0;
  }

  span.proposito:not(:empty) {
  	font-family: 'Inter', 'Arial';
  	font-family: var(--sans-font);
  	font-weight: 600;
  }

  span.proposito:not(:empty):after {
  	content: '. ';
  }

  p.redator {
  	font-size: calc( 16px - 1.5pt );
  	font-size: calc( var(--body-text-font-size) - 1.5pt );
  	font-weight: 500;
  }
	
	p.autor {
		font-family: 'Inter', 'Arial';
		font-family: var(--sans-font);
		font-size: calc(11pt - 1pt);
		font-size: calc(var(--default-font-size) - 1pt);
	}

	p.autor.floating {
		position: absolute;
		width: 2.0cm;
		bottom: 5cm;
		right: 0.5cm;
	}

  div.footer {
  	position: fixed;
  	height: 24px;
  	bottom: 14px;
  	left: 0.6cm;
  	left: var(--margin-left-page);
  	right: 0;
  	font-family: 'Inter', 'Arial';
  	font-family: var(--sans-font);
  	background-color: white; 
  }	

  div.footer fieldset {
  	height: 20px;
  	background-color: white;
  }

  td.content-space-below {
  	height: 40px;
  }

  .tci {
  	margin-top: 40px;
  	font-family: 'Inter', 'Arial';
  	font-family: var(--sans-font);
  	font-size: calc( 11pt - 1pt );
  	font-size: calc( var(--default-font-size) - 1pt );
/*
  	border-top: 1.45px dotted black;
*/  	
  	background-color: white;

  }

  .tci td {
  	font-size: calc( 11pt - 1pt );
  	font-size: calc( var(--default-font-size) - 1pt );
  	height: 0.75cm;
  	vertical-align: bottom;
  }

  .tci h3 {
  	text-align: center;
  	font-weight: 700;
  }

  .tci h3 .light {
  	font-weight: 300;
  }

  td.border-bottom {
  	border-bottom: 1px dashed black;
  	width: 95%;
  }

  div.campolivre {
  	margin-top: 24px;
