  :root {
  	--border: 1px solid #aaa;
  	--sidebar-width: 240px;
  	--sidebar-background-color: #eee;
  	--rightbar-width: 320px;
  }	

	body {
		padding:0;
		margin: 0;
    font-family: 'Inter', 'Segoe UI';
    zoom: calc(15 / 14);   /* SUPER HACK */
	}
	
	.app-frame {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: flex;
		flex-direction: column;
	}

	.topbar {
		padding: 0 0;
	}

	.sidebar-inner {
		width: var(--sidebar-width);
		height: 100%;
		overflow: hidden;
	}

	.bp3-dark .sidebar-inner {
    background-color: rgb(57, 75, 89);
	}
	
	.content {
		flex: 1;
		background-color: white;
	}
	
	.overflow-auto {
		overflow-y: auto;		
	}
	
	.flex1 {
		flex: 1;
	}

