
	.despacho {
		font-size: 13px;
		line-height: 140%;
	}

	.despacho ~ .despacho {
		border-top: 1px dotted #aaa;
		margin-top: 8px;
		padding-top: 8px;
	}
	
	:global(.despacho) ~ :global(.despacho) {
		margin-top: 12px;
		padding-top: 8px;
		border-top: 1px dotted #aaa;
	}
	
	.despacho-usuario {
		font-size: 14px;
		margin-bottom: 8px;
		line-height: 140%;
	}
	
	.despacho-usuario .de-para {
		display: inline-block;
		width: 2.4em;
	}

  .despacho pre {
    font-family: var(--sans-font);
    white-space: pre-wrap;
  }