/* 
	 Este CSS serve para customizar a barra de título de aplicativos 
   Electron que utilizem custom-electron-titlebar 
*/

.menubar-menu-title {
  font-size: 14px !important;
  font-weight: 500;
}

.action-item.focused a {
  text-decoration: none !important;
}

.action-label {
  font-size: 14px !important;
}

.menu-item-icon {
	display: inline-block !important;
	opacity: 0;
	width: 4px !important;
}