:root {
  --border-color: #888;
  --border-color-hover: #222;
  --border-color-focus: rgba(0, 116, 232, 1);
  --border-radius: 4px;
  --field-margin-bottom: 8px;
  --font-family: 'Inter', 'Roboto', 'Segoe UI',  'Arial', sans-serif;
  --font-size: 14px;
  --font-size-floating-label: 11px;
  --color-label: #333;
  --background-color: #ffffff;
  --margin-between-fields: 8px;
}

div.formfield {
  position: relative;
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius);
  margin-bottom: var(--field-margin-bottom);
  background-color: var(--background-color);
  padding-top: 2px;
  padding-bottom: 2px;
  display: flex;
  flex-direction: row;
}

div.formfield.disabled {
  opacity: 0.75;
}

div.formfield.has-error {
  border-color: firebrick;
}

div.formfield:not(.disabled):hover {
  border-color: var(--border-color-hover);
}

div.formfield:focus-within {
  border-color: var(--border-color-focus);
  box-shadow: 0px 0px 1px 1px var(--border-color-focus);
}

div.formfield > * {
  font-family: var(--font-family);
  font-size: var(--font-size);
}

div.formfield > input,
div.formfield > select,
div.formfield > div.embedded {
  flex: 1;

  padding: 8px 8px;
  padding-top: 20px;
  padding-bottom: 4px;
  display: block;

  border: none;
  outline: none;
  background-color: transparent;
  box-sizing: border-box;
}

div.formfield > div.embedded {
  line-height: 10%;
  height: inherit;
}

div.formfield.hasbutton > input,
div.formfield.hasbutton > select,
div.formfield.hasbutton > div {
  width: calc(100% - 32px);
}

div.formfield > label {
  font-family: var(--font-family);
  font-size: var(--font-size);
  color: var(--color-label);
  position: absolute;
  top: calc(50% - 1.25em / 2);
  left: 8px;
  user-select: none;
  pointer-events: none;
  cursor: text;
  transition-property: all;
  transition-duration: 0.25s;
}

div.formfield > button {
  flex: 0;

  border: none;
  border-top-right-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  background-color: transparent;
  padding-left: 8px;
  padding-right: 8px;
  margin-top: -2px;
  margin-bottom: -2px;
  outline: none;  
}

div.formfield > button:hover,
div.formfield > button:focus {
  background-color: #eee;
}

div.formfield > button:active {
  background-color: #ccc;
  padding-top: 1px;
}

div.formfield > textarea {
  display: block;
  margin-top: 20px;
  margin-bottom: 0px;  
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 2px;
  border: none;
  outline: none;
  background-color: transparent;
  box-sizing: border-box;
  line-height: 150%;
  width: 100%;
}

div.formfield > select {
  cursor: default;
}

div.formfield > select ~ label {
  cursor: default;
}

div.formfield > input:valid ~ label,
div.formfield > input:focus ~ label,
div.formfield > textarea:valid ~ label,
div.formfield > textarea:focus ~ label,
div.formfield > select:valid ~ label,
div.formfield > select:focus ~ label,
div.formfield > select:placeholder-shown ~ label,
div.formfield > div.embedded:focus-within ~ label,
div.formfield.not-empty > * ~ label {
  top: 5px;
  font-size: var(--font-size-floating-label);
}

div.formfield > select {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
}

div.formfield select {
  background-image:
    linear-gradient(45deg, transparent 50%, #555 50%),
    linear-gradient(135deg, #555 50%, transparent 50%);
    
  background-position:
    calc(100% - 14px) calc(1em + 4px),
    calc(100% - 10px) calc(1em + 4px);
  background-size:
    5px 5px,
    5px 5px,
    1px 1.5em;
  background-repeat: no-repeat;
}

div.formfield ~ div.message:not(:empty) {
  font-size: 12px;
  text-align: left;
  margin-top: -6px;
  margin-bottom: 6px;
  margin-left: 4px;
  color: #444;
}

div.formfield ~ div.message.error:not(:empty) {
  color: firebrick;
  font-weight: 600;
}


div.gridrow {
  display: flex;
}

div.gridrow > div.formfield {
  display: inline-block;
  margin-bottom: 0;
  margin-left: var(--margin-between-fields);
  margin-right: 0;
  margin-bottom: var(--margin-between-fields);
  flex: 1;
}

div.gridrow > div.formfield:first-of-type {
  margin-left: 0;
}

div.gridrow > div.l1 {
  min-width: 8.33%;
  flex: 0;  
}

div.gridrow > div.l2 {
  min-width: 16.66%;
  flex: 0;
}

div.gridrow > div.l3 {
  min-width: 25%;
  flex: 0;
}

div.gridrow > div.l4 {
  min-width: 33.33%;
  flex: 0;
}

div.gridrow > div.l5 {
  min-width: 41.66%;
  flex: 0;
}

div.gridrow > div.l6 {
  min-width: 50%;
  flex: 0;
}

div.gridrow > div.l9 {
  min-width: 75%;
  flex: 0;
}


.bp3-input-group.embedded input {
   background-color: transparent;
   padding: 0px 0px;
   box-shadow: none;
   height: inherit;
   line-height: inherit;
}

.bp3-input-group.embedded input:focus {
  background-color: transparent;
  outline: none;
  border: none;
  box-shadow: none;
}

.embedded .bp3-input {
  box-shadow: none;
  padding: 0;
  margin-left: 0px;
  margin-bottom: -4px;
}

.embedded .bp3-tag-input-values {
  padding: 0;
  margin: 0;
  margin-left: -2px;
  margin-bottom: -2px;
}

.embedded .bp3-input-ghost {
  padding-left: 3px;
  padding-left: 0;
  margin-left: -2px;
/*  
  width: auto;
  flex-basis: 40px;
*/
  width: 60px;  
}

/* Customization for Suggest component */

.embedded.suggest .bp3-input {
  height: 28px;
  line-height: 150%;
}

.embedded.suggest ~ .bp3-icon {
  transform: translateY(24px);
  padding-right: 8px;
  color: rgba(0, 0, 0, 0.75);
  cursor: pointer;
  transition: opacity 0.5s ease;
}

/* Needed customization for Blueprint's Button */

button.bp3-button {
  outline-color: transparent;
  outline-offset: -3px;
}

button.bp3-button:focus {
  outline-color: rgba(92, 112, 128, 0.5);
}

/* Customizações para o FloatingSelect */

.bp3-multi-select-popover .bp3-menu,
.bp3-select-popover .bp3-menu {     
  max-height: 310px;
  overflow-y: auto;
}    

