.mascara-panel {
	flex: 0;
	--width: var(--rightbar-width);
	flex-basis: var(--width);
	width: var(--width);
	height: 100%;
	background-color: #eee;	
	background-color: var(--sidebar-background-color);
	overflow-y: hidden;
	padding: 10px 0px;
	padding-bottom: 0;
	display: flex;
	flex-direction: column;
}

.mascara-panel::-webkit-scrollbar,
.mascara-panel *::-webkit-scrollbar {
  width: 8px;
}
 
.mascara-panel::-webkit-scrollbar-track,
.mascara-panel *::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
.mascara-panel::-webkit-scrollbar-thumb,
.mascara-panel *::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: none;
}

div.show {
	display: block;
}

div.hide {
	display: none;
}

.mascara-tab {
	flex: 0;
}

/* 
  Customizações de classes do Blueprint.js 
*/  

.mascara-tab .bp3-tab-list {
	border-bottom: 1px solid #aaa;
	padding-left: 16px;
	margin-top: -1px;  /* Usado para alinhar o baseline do tab com a barra de ícones do TinyMCE */
}

.mascara-tab .bp3-tab-panel {
	margin-top: 12px;
}

.mascara-tab .bp3-tab {
	outline: none;
}


