:root {
	--animation-duration: .3s;
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  transform: translateX(-100%);
  z-index: 0;
}
	
.slider.right	{
	transform: translateX(100%);
}

.slide-in {
  animation: slide-in var(--animation-duration) forwards;
}
	
.slide-in.right {
	animation: slide-in-right var(--animation-duration) forwards;
}

.slide-out {
  animation: slide-out var(--animation-duration) forwards;
}
	
.slide-out.right {
	animation: slide-out-right var(--animation-duration) forwards;
}	
    
@keyframes slide-in {
	0%   { transform: translateX(-100%); opacity: 0; }
  100% { transform: translateX(0%); opacity: 1; }
}

@keyframes slide-in-right {
	0%   { transform: translateX(100%); opacity: 0;  }
	100% { transform: translateX(0%); opacity: 1; }
}
	

@keyframes slide-out {
  0%   { transform: translateX(0%); opacity: 1; }
	80%  { opacity: 0; }
  99%  { transform: translateX(-100%); }
  100% { transform: scaleY(0); }
}
	
@keyframes slide-out-right {
	0%   { transform: translateX(0%); opacity: 1; }
	80%  { opacity: 0; }
	99%  { transform: translateX(100%); opacity: 0;  }
	100% { transform: scaleY(0); }
}	
