.rubrica {
	padding-left: 4px;
	padding-right: 4px;
}

.rubrica ~ .rubrica {
	border-top: 1px dotted #aaa;
	margin-top: 8px;
	padding-top: 8px
}
 

.rubrica-unidade {
	font-size: 16px;
	font-weight: 600;
}

.rubrica-status {
	font-size: 11px;
	font-weight: 600;
	padding: 4px 8px;
  --background-color: gray;	
	background-color: var(--background-color);
	color: white;
	border-radius: 6px;
	margin-left: 12px;
	transform: translateY(-3px);
	display: inline-block;
	text-transform: uppercase;
	font-size: 9px; 
}

.rubrica-status.aprovado {
	--background-color: #2e7d32;
}

.rubrica-status.rejeitado {
	--background-color: #d50000;

}	

.rubrica-status.ressalvas {
	--background-color: #ff6d00;
}	

.rubrica-comentarios {
	font-size: 13px;
	font-weight: 400;
	/* letter-spacing: -0.2px;		*/
	margin-top: 8px;
	line-height: 135%
}

.rubrica-autor {
	font-size: 13px;
	margin-top: 8px;
}

.rubrica pre {
  font-family: var(--sans-font);
  white-space: pre-wrap;
}
